const { createStore, createHook } = require("react-sweet-state");
import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import queryString from "query-string";
import { toast } from "react-toastify";
const ChiTietNganhDaoTao = "ChiTIetNganhDaoTaoStore";
import i18n from "i18n/i18n";

const initialState = {
    data: [],
    total: 0,
    page: 0,
    isError: false,
    message: "",
    item: null,
};

const actions = {
    getChiTietNganhDaoTao: (filter) => async ({ setState }) => {
        try {
            const query = queryString.stringify({ ...filter });
            const results = await axiosClient.get(
                `${CONFIG.API.CHITIETNGANHDAOTAO}?${query}`
            );
            if (results) {
                const { contents = [], total = 0, page = 0 } = results;
                setState({
                    data: contents,
                    total,
                    page,
                    isError: false,
                });
            } else {
                setState({
                    data: [],
                });
            }
        } catch (error) {
            const data = error?.response?.data;
            toast.error(i18n.t(`${data?.message || "ERROR"}`));
            setState({
                isError: true,
                data: [],
                item: null,
            });
        }
    },
    getChiTietNganhDaoTaoID: (param) => async ({ setState }) => {
        try {
            const results = await axiosClient.get(
                `${CONFIG.API.CHITIETNGANHDAOTAO}/${param}`
            );
            if (results) {
                setState({
                    item: { ...results },
                });
            } else {
                setState({
                    item: null,
                });
            }
        } catch (error) {
            setState({
                isError: true,
            });
        }
    },
};
const Store = createStore({
    initialState,
    actions,
    name: ChiTietNganhDaoTao,
});
const useChiTietNganhDaoTao = createHook(Store);
export default useChiTietNganhDaoTao;
