import React, { useState, useEffect } from "react";
import { TYPE_MAMANGE_PROGRAM_PAGE } from "constants/constants";
import ManageProgramLayout from "layouts/ManageProgramLayout/ManageProgramLayout";
import ManageProgramEducationBasic from "./ManageProgramEducation";
import { useParams } from "react-router-dom";

const ManageProgramEducationWrapper = ({ isLogined }) => {
  const { khoa = "", nganhDaoTao = "", type = "" } = useParams();
  const [typeManageProgram, setTypeManageProgram] = useState(
    TYPE_MAMANGE_PROGRAM_PAGE.BASIC
  );
  useEffect(() => {
    setTypeManageProgram(type);
  }, [khoa, nganhDaoTao, type]);
  const onChangeTypeManage = (typeManage) => {
    setTypeManageProgram(typeManage);
  };
  const renderContent = () => {
    switch (typeManageProgram) {
      case TYPE_MAMANGE_PROGRAM_PAGE.STRUCT:
        return <div>STRUCT</div>;
      case TYPE_MAMANGE_PROGRAM_PAGE.CONTENT:
        return <div>CONTENT</div>;
      case TYPE_MAMANGE_PROGRAM_PAGE.PLAN:
        return <div>PLAN</div>;
      case TYPE_MAMANGE_PROGRAM_PAGE.BASIC:
        return (
          <>
            <ManageProgramEducationBasic />
          </>
        );
    }
  };

  return (
    <ManageProgramLayout
      isLogined={isLogined}
      pathName={typeManageProgram}
      onChangeTypeManage={onChangeTypeManage}
    >
      {renderContent()}
    </ManageProgramLayout>
  );
};

export default ManageProgramEducationWrapper;
