import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Col, Row, Button, Label, Input } from "reactstrap";
import useChiTietNganhDaoTao from "stores/admin/CTNganhDaoTaoStore/CTNganhDaoTaoStore";
import { useParams } from "react-router-dom";
import Loading from "components/common/loading/Loading";
import "./ManageProgramEducation.css";

const ManageProgramEducationBasic = (props) => {
  const { t } = useTranslation();
  const { nganhDaoTao = "", khoa = "" } = useParams();
  const [isLoading, setIsLoading] = useState(false);
  const [
    stateChiTietNganhDaoTao,
    actionChiTietNganhDaoTao,
  ] = useChiTietNganhDaoTao();
  const [chiTietNganhDaoTao, setChiTietNganhDaoTao] = useState(null);
  useEffect(() => {
    (async () => {
      setIsLoading(true);
      await actionChiTietNganhDaoTao.getChiTietNganhDaoTao({
        khoa,
        nganhDaoTao,
      });
      setIsLoading(false);
    })();
  }, [nganhDaoTao, khoa]);

  useEffect(() => {
    setChiTietNganhDaoTao(stateChiTietNganhDaoTao?.data[0] || {});
  }, [stateChiTietNganhDaoTao?.data]);

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
        <div className="ManageProgramEducationBasic__container">
          <Row className="ManageProgramEducation_background ManageProgramEducation__tools">
            <Button outline className="btn" color="success">
              {t("SAVE")}
            </Button>
            <Button outline className="btn" color="danger">
              {t("ROLLBACK")}
            </Button>
          </Row>
          <Row className="ManageProgramEducation_background ManageProgramEducation__contents">
            <Col>
              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label>
                        <b>{t("PROGRAM_NAME")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="input"
                        name="programName"
                        id="programName"
                        placeholder={`${t("ENTER_PROGRAM_NAME")}`}
                        defaultValue={
                          chiTietNganhDaoTao?.nganhDaoTao?.chuongTrinhDaoTao
                            ?.ten || ""
                        }
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>

              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label>
                        <b>{t("UNIVERSITY_DEGREE")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="universityDegree"
                        name="selectDegree"
                        id="universityDegree"
                        placeholder={t("ENTER_UNIVERSITY_DEGREE")}
                        defaultValue={
                          chiTietNganhDaoTao?.nganhDaoTao?.chuongTrinhDaoTao
                            ?.trinhDo || ""
                        }
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>

              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label>
                        <b>{t("MAJORS")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="major"
                        name="selectMajor"
                        id="major"
                        placeholder={t("ENTER_MAJORS")}
                        defaultValue={
                          chiTietNganhDaoTao?.nganhDaoTao?.ten || ""
                        }
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>

              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label>
                        <b>{t("MAJORS_CODE")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="majorCode"
                        name="majorCode"
                        id="majorCode"
                        placeholder={t("ENTER_MAJORS_CODE")}
                        defaultValue={
                          chiTietNganhDaoTao?.nganhDaoTao?.maNganhDaoTao || ""
                        }
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>

              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label for="exampleEmail">
                        <b>{t("TYPE_EDUCATION")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="typeEducation"
                        name="typeEducation"
                        id="typeEducation"
                        placeholder={t("ENTER_TYPE_EDUCATION")}
                        defaultValue={
                          chiTietNganhDaoTao?.nganhDaoTao?.chuongTrinhDaoTao
                            ?.loaiHinh || ""
                        }
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>

              <Row className="item-label">
                <Col>
                  <Row>
                    <Col>
                      <Label for="exampleEmail">
                        <b>{t("SCHOOL_YEAR")}</b>
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Input
                        className="schoolYear"
                        name="schoolYear"
                        id="schoolYear"
                        placeholder={t("ENTER_SCHOOL_YEAR")}
                        defaultValue={chiTietNganhDaoTao?.khoa || ""}
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>
            </Col>
          </Row>
        </div>
      )}
    </>
  );
};

export default ManageProgramEducationBasic;
