import { RoutesString } from "pages/routesString";
import React from "react";
import { useTranslation } from "react-i18next";
import { Link, useParams } from "react-router-dom";
import "./SideBarManage.css";

const defaultProps = {
  menus: [
    { label: "basic", value: "INFORMATION_BASIC" },
    { label: "struct", value: "STRUCTURE_BASIC" },
    { label: "content", value: "CONTENT_PROGRAM" },
    { label: "plan", value: "PLAN_EDUCATE" },
  ],
};

const SideBarManage = ({
  menus = defaultProps.menus,
  pathName,
  onChangeTypeManage,
}) => {
  const { t } = useTranslation();
  const { khoa = "", nganhDaoTao = "" } = useParams();
  return (
    <div>
      {menus.map((item) => (
        <Link
          className="linkItem"
          to={`${RoutesString.MANAGE_PROGRAM_EDUCATION.replace(
            ":nganhDaoTao",
            nganhDaoTao
          )
            .replace(":khoa", khoa)
            .replace(":type", item?.label)}`}
        >
          <span
            key={item?.label}
            className={`circle ${pathName?.includes(item?.label) && "round"}`}
          >
            <b className="itemName">{t(item?.value)}</b>
          </span>
        </Link>
      ))}
    </div>
  );
};

export default SideBarManage;
